import React from "react";
import { Pricing } from "../Components/pricing";
import { Jumbotron } from "../Components/jumbotron";

export const PricingPage = () => {
  return (
    <div className="container rounded">
      <Jumbotron
        heading="Information fr Privatpatienten"
        text="Es ist bedauerlicherweise immer wieder der Fall, dass private
          Krankenversicherungen versuchen, ihren Kunden einen Teil der
          rechtmig erhobenen Honorare fr Heilmittel vorzuenthalten und
          Rechnungen zu krzen. In den meisten Fllen geschieht dies zu Unrecht
          , wie verschiedene Gerichtsurteile in den letzten Jahren besttigt
          haben. Im Bereich der privaten Krankenversicherung gibt es keine
          allgemeine verbindliche Gebhrenordnung fr physiotherapeutische
          Leistungen. Bei der Preisgestaltung orientiere ich mich an den
          Empfehlungen aus der Gebhrenbersicht fr Therapeuten. (GebTH) Diese
          bersicht dient als Rahmen fr Festlegung von Privatpatienten in der
          Physiotherapie und anderen Bereichen der Heilmitteltherapie. Fr
          Privatpatienten und Selbstzahler erlaubt der Gesetzgeber die Anwendung
          eines Multiplikators zur Kostendeckung der physiotherapeutischen
          Leistungen, der zwischen 1,4 und 2,3 liegen. Meine Preise liegen
          zwischen 1,4 und 1,8."
      />

      <Pricing />
    </div>
  );
};
