import React from "react";
import Accordion from "react-bootstrap/Accordion";
export const Accordeon = () => {
  return (
    <div className="container mt-3">
    <Accordion>
      <Accordion.Item eventKey="0">
        <Accordion.Header ><div className="h4">Krankengymnastik</div></Accordion.Header>
        <Accordion.Body>
          Krankengymnastik ist eine Therapiemethode, die darauf abzielt, die
          Funktionen des Bewegungsapparates wiederherzustellen und Schmerzen zu
          reduzieren. Dabei werden gezielte bungen, Handgriffe und Bewegungen
          eingesetzt, die auf die spezifischen Beschwerden des Patienten
          abgestimmt sind. Das Ziel ist es, die Beweglichkeit zu verbessern, die
          Muskulatur zu strken und die Krperhaltung zu optimieren. Die
          Krankengymnastik wird bei verschiedenen Erkrankungen des
          Bewegungsapparates eingesetzt, wie zum Bsp. bei Rckenschmerzen,
          Gelenkproblemen oder nach Verletzungen.
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="1">
        <Accordion.Header>
        <div className="h4">Propriozeptive Neuromuskulre Fazilitation ( PNF) </div>
        </Accordion.Header>
        <Accordion.Body>
          Die Propriozeptive Neuromuskulre Fazilitation (PNF) ist eine
          Therapiemethode, die in der Physiotherapie angewendet wird. Sie wird
          bei Patienten aus verschiedenen medizinischen Fachbereichen
          eingesetzt, bei denen das Bewegungsverhalten aufgrund von Krankheit,
          Verletzungen, Operationen oder Degeneration beeintrchtigt ist.
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="2">
        <Accordion.Header><div className="h4">Manuelle Therapie</div></Accordion.Header>
        <Accordion.Body>
          Die manuelle Therapie wird eingesetzt, um Funktionsstrungen des
          Bewegungsapparates zu behandeln. Diese knnen durch falsche Haltung,
          berlastung, Verschlei oder Verletzung entstehen. Bei der Behandlung
          werden Manipulation-und Mobilisationstechniken angewendet, um
          Bewegungseinschrnkungen an den betroffenen Gelenken zu lsen.
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="3">
        <Accordion.Header><div className="h4">Manuelle Lymphdrainage</div></Accordion.Header>
        <Accordion.Body>
          Das Lymphsystem ist ein wichtiges Netzwerk im Krper, das in
          Verbindung mit den Blutgefen steht. Es ist das Zentrum des
          Immunsystems und versorgt die Zellen. Die Lymphflssigkeit
          transportiert Abfallprodukte ab und beeinflusst somit den ganzen
          Krper. Die Lymphdrainage wir oft von rzten verschrieben, um
          Stauungen bei demen, nach Operationen, Verletzungen oder
          Krebserkrankungen zu behandeln. Es ist wichtig, dem Lymphsystem
          Aufmerksamkeit zu schenken.
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="4">
        <Accordion.Header><div className="h4">Klassische Massagen</div></Accordion.Header>
        <Accordion.Body>
          Die klassische Massage ist eine weit verbreitete Massageform, bei der
          verschiedene Griffe und Techniken angewendet werden, um ein
          funktionelles Gleichgewicht zu erreichen. Dabei werden nicht nur die
          Muskeln behandelt, sondern der gesamte Organismus positiv beeinflusst.
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="5">
        <Accordion.Header><div className="h4">Eis - Kryotherapie</div></Accordion.Header>
        <Accordion.Body>
          Die Anwendung von Eis ist eine Ergnzung zu den nachfolgenden
          Behandlungen. Sie wird bei akuten Schmerzen eingesetzt.
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="6">
        <Accordion.Header><div className="h4">Wrmetherapie</div></Accordion.Header>
        <Accordion.Body>
          In der Physiotherapie wird die Wrmetherapie als ergnzende Manahme
          betrachtet. Bestehend aus einem Wrmetrger oder einer heien Rolle,
          dient sie dazu die Hauptbehandlung zu untersttzen, insbesondere um
          verspannte Muskulatur zu lsen.
        </Accordion.Body>
      </Accordion.Item>
    </Accordion>
    </div>
  );
};
